// @ts-ignore
import React, { useState, useEffect } from "react";
import { neos } from "@neos-project/neos-ui-decorators";
import { IconButton, SelectBox } from "@neos-project/react-ui-components";
import { generateColorObject, createSelectBoxOptions, colorReturnValues, capitalizeFirstLetter, getPreviewBoxStyle } from "./Utlis";
import * as stylex from "@stylexjs/stylex";
import Error from "./Error";
import OptionPreview from "./OptionPreview";
import "./Tokens.stylex";
import { colors, sizes, fonts } from "./Tokens.stylex";
import type { StateObject } from "./Utlis";
const neosifier = neos(globalRegistry => ({
  i18nRegistry: globalRegistry.get("i18n"),
  config: globalRegistry.get("frontendConfiguration").get("CarbonTailwindColors")
}));
const defaultProps = {
  options: {
    allowEmpty: false,
    disabled: false,
    placeholder: null,
    scheme: null,
    disableStrength: []
  }
};
const styles = {
  wrapper: {
    display: "x78zum5",
    flexDirection: "xdt5ytf",
    gap: "xoat92z",
    $$css: true
  },
  disabled: {
    cursor: "x1h6gzvc",
    opacity: "x190dgpg",
    ":where(*) *_pointerEvents": "x1g8rrie",
    $$css: true
  },
  previewBox: {
    flex: "x98rzlu",
    height: "x1nv6co2",
    lineHeight: "x1pl0vzw",
    border: "xq15zi8",
    borderRadius: "x1dwtru6",
    display: "x78zum5",
    alignItems: "x1pha0wt",
    paddingInline: "x7ks0zz",
    gap: "xoat92z",
    overflow: "xb3r6kr",
    textEllipsis: "x1sect9i",
    $$css: true
  },
  checkboard: {
    backgroundImage: "x1eyyrmo",
    backgroundSize: "x1ht9rhz",
    $$css: true
  },
  highlight: {
    borderRadius: "x1dwtru6",
    boxShadow: "xypt998",
    $$css: true
  },
  colorListItem: {
    height: "xpdco3j",
    cursor: "x1ypdohk",
    border: "x1wty727",
    padding: "x1717udv",
    flex: "x98rzlu",
    ":first-child_borderTopLeftRadius": "xpryen9",
    ":first-child_borderBottomLeftRadius": "xqgiiig",
    ":last-child_borderTopRightRadius": "xn9o1y4",
    ":last-child_borderBottomRightRadius": "x192clpw",
    ":hover_boxShadow": "x1kstqak",
    ":hover_zIndex": "x1h71102",
    ":focus_boxShadow": "xovzxhn",
    ":focus_zIndex": "x11hdtt0",
    $$css: true
  },
  colorListItemActive: {
    boxShadow: "x1dl3sty",
    zIndex: "x1vjfegm",
    $$css: true
  }
};

// @ts-ignore
function Editor(props) {
  const options = {
    ...defaultProps.options,
    ...props.config,
    ...props.options
  };
  const {
    value,
    commit,
    highlight,
    i18nRegistry
  } = props;
  const colors = generateColorObject({
    colors: options.colors,
    scheme: options.scheme,
    grayscale: options.grayscale,
    disableStrength: options.disableStrength
  });
  if (!colors) {
    return <Error>{i18nRegistry.translate("Carbon.TailwindColors:Main:noColorsDefined")}</Error>;
  }
  const {
    allowEmpty,
    disabled,
    placeholder
  } = options;
  const selectBoxOptions = createSelectBoxOptions(colors);
  const [state, setState]: [StateObject, any] = useState(colorReturnValues({
    group: value?.group,
    strength: value?.strength,
    colors
  }));
  useEffect(() => {
    if (!state?.hex) {
      if (value != "") {
        commit("");
      }
      return;
    }
    if (JSON.stringify(value) != JSON.stringify(state)) {
      commit(state);
    }
  }, [state]);
  function handleColorClick(group: string, strength: number) {
    setState(colorReturnValues({
      group,
      strength,
      colors
    }));
  }
  function handleGroupChange(group: string) {
    const strength = state?.strength || null;
    setState(colorReturnValues({
      group,
      strength,
      colors
    }));
  }
  const i18n = {
    white: i18nRegistry.translate("Carbon.TailwindColors:Main:white"),
    black: i18nRegistry.translate("Carbon.TailwindColors:Main:black"),
    selectColor: i18nRegistry.translate("Carbon.TailwindColors:Main:selectColor"),
    resetColor: i18nRegistry.translate("Carbon.TailwindColors:Main:resetColor"),
    selectColorGroup: i18nRegistry.translate("Carbon.TailwindColors:Main:selectColorGroup")
  };
  const colorsArray = [];
  for (const group in colors) {
    const groupValues = colors[group];
    const label = capitalizeFirstLetter(group);
    const values = [];
    for (const strength in groupValues) {
      const color = groupValues[strength];
      let label = strength;
      if (label == "0") {
        label = i18n.white;
      } else if (label == "1000") {
        label = i18n.black;
      }
      values.push({
        strength: typeof strength == "string" ? parseInt(strength) : strength,
        label,
        color
      });
    }
    colorsArray.push({
      group,
      label,
      values
    });
  }
  return <div {...stylex.props(styles.wrapper, disabled && styles.disabled)}>
            <div {...{
      className: "x78zum5 xoat92z"
    }}>
                <div {...stylex.props(styles.previewBox, !placeholder && !state?.hex && styles.checkboard, highlight && styles.highlight)} style={getPreviewBoxStyle({
        colors,
        state,
        placeholder
      })}>
                    {state?.hex && state?.group && <>
                            {state?.strength == 1000 && i18n.black}
                            {state?.strength == 0 && i18n.white}
                            {state?.strength != 0 && state?.strength != 1000 && <>
                                    <span>{capitalizeFirstLetter(state.group)}</span>{" "}
                                    <span {...{
              className: "xamkqhe"
            }}>{state?.strength}</span>
                                </>}
                        </>}
                </div>
                {allowEmpty && <IconButton style="lighter" icon="times" title={i18n.resetColor} onClick={() => {
        setState(null);
      }} />}
            </div>
            <SelectBox options={selectBoxOptions} value={state?.group} placeholder={i18n.selectColorGroup} allowEmpty={false} onValueChange={handleGroupChange} ListPreviewElement={OptionPreview} />
            {colorsArray.map(item => {
      const isActiveGroup = state && state.group == item.group;
      return isActiveGroup && <div {...{
        className: "x1n2onr6 x78zum5 x195vfkc x1dwtru6"
      }}>
                            {item.values.map(entry => {
          const current = entry.strength == state.strength;
          return <button {...stylex.props(styles.colorListItem, current && styles.colorListItemActive)} key={item.group + entry.strength} style={{
            backgroundColor: entry.color
          }} title={entry.label} onClick={() => handleColorClick(item.group, entry.strength)}></button>;
        })}
                        </div>;
    })}
        </div>;
}
export default neosifier(Editor);